.character {
  position: relative;

  &__inner {
    padding: 44px 75px 0;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    text-indent: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
  }

  &__pic {
    display: flex;
    justify-content: center;
  }

  &__img {
    border-radius: 3px;
  }
}