.family {
  position: relative;
  
  &__inner {
    display: flex;
    flex-direction: column;
    padding: 20px 75px 0;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 1.5;
    text-align: justify;
    text-indent: 20px;
  }

  &__pic {
    display: flex;
    justify-content: center;
  }

  &__img {
    border-radius: 5px;
  }
}