.diplomasfirst {
  position: relative;
  &__inner {
    display: flex;
    flex-direction: column;
    padding: 45px 75px 0;
  }
  
  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__row {
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}

.diplomas {
  position: relative;

  &__swiper {
    margin: 50px auto 0;
    width: 400px;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__img {
    margin-bottom: 20px;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
  }

  &__arrow {
    color: #000000;

    &-prev {
      transform: translateX(100px);
    }

    &-next {
      transform: translateX(-100px);
    }
  }
}