.plans {
  position: relative;

  &__inner {
    @include flex-sb;
    padding: 50px 75px 0;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__pic {
    width: 50%;
  }

  &__img {
    margin-top: 50px;
    border-radius: 3px;
  }

  &__info {
    margin-top: 100px;
    width: 50%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    text-indent: 30px;
    line-height: 1.5;
  }
}