.reviews {
  position: relative;

  &__inner {
    @include flex-sb;
    padding: 20px 75px 0;
  }

  &__info {
    width: 100%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    text-indent: 20px;
    font-size: 18px;
    line-height: 1.5;
  }

  &__sign {
    margin: 0;
    margin-right: 50px;
    margin-bottom: 50px;
    text-align: right;
  }
}