.learning {
  position: relative;
  &__inner {
    @include flex-sb;
    padding: 100px 75px 0;
  }

  &__pic {
    display: flex;
    width: 50%;
    justify-content: end;
  }

  &__img {
    margin-top: 50px;
    border-radius: 3px;
  }

  &__info {
    width: 50%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    text-indent: 20px;
    font-size: 18px;
    line-height: 1.5;
  }
}