.name {
  position: relative;
  
  &__inner {
    @include flex;
    flex-direction: column;
    padding: 0 75px;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;

    text-align: center;
    font-weight: 500;
    font-size: 25px;
    line-height: 30px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;

    &:last-child {
      margin-bottom: 35px;
    }
  }

  &__pic {
    @include flex;
    justify-content: center;
    margin-bottom: 20px;
  }

  &__img {
    border-radius: 3px;
  }

  &__info {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    text-align: justify;
    text-indent: 20px;
  }
}