.interests {
  position: relative;

  &__inner {
    @include flex-sb;
    padding: 0 75px;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__row {
    @include flex-sb;
    margin-top: 50px;
    margin-bottom: 100px;
  }

  &__pic {
    width: 30%;

    &-right {
      justify-content: end;
    }
  }

  &__img {
    width: 150px;
    height: 150px;
    border-radius: 100px;
  }

  &__text {
    width: 70%;
    margin: 0;
    margin-top: 30px;
    font-size: 18px;
    text-indent: 30px;
    line-height: 1.3;
    text-align: justify;
  }
}