.city {
  position: relative;

  &__inner {
    padding: 200px 75px 0;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    text-indent: 30px;
    font-size: 18px;
    line-height: 1.5;
    text-align: justify;
  }
}