.friends {
  position: relative;

  &__inner {
    @include flex-sb;
    padding: 20px 75px 0;
  }

  &__pic {
    display: flex;
    width: 50%;
    justify-content: end;
  }

  &__img {
    margin-top: 10px;
    border-radius: 3px;
  }

  &__info {
    width: 50%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-top: 25px;
    margin-bottom: 20px;
    text-indent: 30px;
    font-size: 18px;
    line-height: 1.3;
  }

  &__down {
    display: flex;
    justify-content: center;
  }

  &__under {
    border-radius: 3px;
  }
}