.hobby {
  position: relative;

  &__inner {
    padding: 30px 75px 0;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__pic {
    display: flex;
    justify-content: center;
  }

  &__info {
    margin-bottom: 40px;
    width: 100%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;

    text-align: justify;
    text-indent: 20px;
  }
}