.contents {
  position: relative;

  &__inner {
    display: flex;
    flex-direction: column;
    padding: 30px 75px 0;
  }

  &__title {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
  }

  &__row {
    @include flex-sb;
    font-size: 18px;
    margin-bottom: 35px;

    &:last-child {
      margin-bottom: 0;
    }
  }



}