.school {
  position: relative;

  &__inner {
    @include flex-sb;
    padding: 60px 75px 0;
  }

  &__pic {
    width: 50%;
  }

  &__img {
    margin-top: 100px;
    border-radius: 3px;
  }

  &__info {
    width: 50%;
    text-align: justify;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    text-indent: 20px;
    line-height: 1.5;
  }
}